body {
  padding-bottom: 20px;
  background-color: #f1f1f1;
}
.navbar {
  margin-bottom: 0;
}
.container {
    min-width: 320px;
    max-width: 640px;
}

.btn-toolbar {
    margin-top: 10px;
    text-align: center;
}

.btn-toolbar .btn-group {
    float: none;
    float: initial;
}

.footer {
    margin-top: 10px;
    padding: 5px 0;
    text-align: right;
}

.footer,
.footer > * {
    font-size: 12px;
    color: #aaa;
}

.footer a {
    text-decoration: none;
}

.footer a:hover {
    color: #666;
}
.container .list-group {
    margin-bottom: 2px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
}

.panel-title-group {
    /* 用于解决内容垂直对齐偏差 */
    font-size: 0;
}

.panel-title-group * {
    /* 内容垂直对齐 */
    vertical-align: middle;
}

.panel-title-group .panel-title-caption {
    margin-left: 3px;
    /* 修复 font-size 为 0 的问题 */
    font-size: 14px;
}

.panel-title-group .badge-icon {
    margin-left: 5px;
    color: #ccc;
    /* 修复 font-size 为 0 的问题 */
    font-size: 12px;
}

.toolbar>* {
    margin-left: 10px;
    font-size: 12px;
    color: #999;
}

.toolbar>a:hover {
    color: #333;
}
.title {
    margin-top: 15px;
    margin-bottom: 5px;
    font-size: 12px;
    color: #999;
    font-weight: 500;
}
.more {
    height: 16px;
    line-height: 16px;
}

.more a {
    font-size: 12px;
    color: #999;
    text-decoration: none;
}

.more a:hover {
    color: #666;
}

.more a span {
    margin-left: 10px;
}
